import React from 'react'
import { useHistory } from 'react-router'
import Logo from '../../assets/LOGO.png'
import "../../styles/Marketplace/Catalog.css"
import { FiSearch, FiShoppingCart } from 'react-icons/fi'


const Catalog: React.FC = () => {
    let history = useHistory()
    return (
        <div className="catalog_container">
            <div className="cat_header">
                <div className="cat_header_first_part">
                    <div className="marketplace_header_logo" onClick={() => history.push('/')}>
                        <img src={Logo} alt="logo" width={80} height={50} />
                    </div>
                    <div className="cat_header_list">
                        <div className="cat_header_item">
                            <div className="cat_header_link" onClick={() => history.push('/marketplace')}>Home</div>
                            </div>
                        <div className="cat_header_item">
                            <div className="cat_header_link" onClick={() => history.push('/marketplace/catalog')}>Catalog</div>
                        </div>
                    </div>
                </div>
                <div className="cat_header_second_part">
                    <div className="cat_icons">
                        <FiSearch size={26}/>
                        <FiShoppingCart size={26}/>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Catalog